input:not(input[type="file"]) {
  background: none;
  border: none;
  border-bottom: 1px solid #000;
  outline: none;
  padding: .5rem;
}

textarea {
  resize: vertical;
  background: none;
  border: none;
  border-left: 1px solid #000;
  outline: none;
}

.test-title, .test-description {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.test-title {
  font-weight: bold;
}

#questions-container {
  margin-top: 20px;
}

.question {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 15px;
  position: relative;
  box-shadow: 0 2px 5px #0000001a;
}

.question-header {
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  display: flex;
}

.question-header label {
  flex-grow: 1;
  align-items: center;
  gap: .5rem;
  margin-right: 10px;
  display: flex;
}

.question-header label input[type="text"] {
  flex: 1;
}

.question select {
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 5px;
}

.answers input[type="text"] {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: calc(100% - 30px);
  padding: 8px;
}

.answer {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: .5rem;
  display: flex;
}

.answer label {
  flex: .7;
  align-items: center;
  display: flex;
}

.answer label input[type="text"] {
  width: 50%;
}

.answer label input[type="radio"], input[type="checkbox"] {
  margin: 0 .5rem;
}

button {
  color: #fff;
  cursor: pointer;
  background: #ee0404;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  transition: all .3s;
}

button:hover {
  background: #db1818;
}

.add-answer {
  background: #007bff;
  margin-top: 10px;
}

.add-answer:hover {
  background: #0056b3;
}

.remove-question {
  background: #d4061b;
  margin-left: 10px;
}

.remove-question:hover {
  background: #70010d;
}

.image-upload {
  margin-top: 10px;
}

.image-preview {
  object-fit: contain;
  width: max-content;
  max-width: 300px;
  margin-top: 10px;
  position: relative;
}

.image-preview img {
  border-radius: 5px;
  max-width: 100%;
  height: auto;
}

.delete-image {
  cursor: pointer;
  background: #dc3545;
  border: none;
  font-size: 14px;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.add-question {
  background: #007bff;
  margin-top: 10px;
}

.add-question:hover {
  background: #0056b3;
}

.make-test {
  background: #0ba739;
  margin-top: 10px;
}

.make-test:hover {
  background: #0e8a02;
}

.drag-handle {
  margin-bottom: 4px;
  margin-right: 10px;
}

.test-content {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.test-content label {
  flex-direction: column;
  flex: 1;
  gap: 1rem;
  display: flex;
}

.test-content label input, textarea {
  width: 50%;
}

.rollback {
  color: #000;
  border-left: 1px solid #28a745;
  padding: .5rem 1rem;
  font-weight: bold;
  text-decoration: none;
}

.add-test-link {
  color: #fff;
  text-decoration: none;
}
